#page-auth {
    display: flex;
    align-items: stretch;
    height: 100vh;
    aside {
      flex: 7;
      background: #835afd;
      color: #ffffff;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 120px 80px;
  
      img {
        max-width: 320px;
      }
      strong {
        font: 700 36px 'Poppins', sans-serif;
        line-height: 42px;
        margin-top: 16px;
      }
  
      p {
        font-size: 24px;
        line-height: 32px;
        margin-top: 16px;
        color: #f8f8f8;
      }
    }
    main {
      flex: 8;
      padding: 0 32px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  
    .main-content {
      display: flex;
      flex-direction: column;
      width: 100%;
      max-width: 340px;
      align-items: stretch;
      text-align: center;
  
      > img {
        align-self: center;
      }

      h2 {
        font-size: 24px;
        margin: 64px 0 24px;
        font-family: 'Poppins', sans-serif; 
      }
  
      form {
        input {
          height: 58px;
          border-radius: 8px;
          padding: 0 16px;
          background: #ffffff;
          border: 1px solid #a8a8b3;
        }
        button {
          margin-top: 20px;
        }
        button, input {
          width: 100%;
        }
      }

      p {
        font-size: 14px;
        color: #a8a8b3;
        margin-top: 16px;

        a {
          color: #e559f9;
        }
      }
    }
    .create-room {
      margin-top: 64px;
      height: 50px;
      border-radius: 8px;
      font-weight: 500px;
      background: #ea4335;
      color: #ffffff;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      border: 0;
      transition: filter 0.4s ease-in-out;
  
      img {
        margin-right: 8px;
      }
  
      &:hover {
        filter: brightness(0.8);
      }
  
    }
    .separator {
      font-size: 14px;
      color: #a8a8b3;
      margin: 32px 0;
      display: flex;
      align-items: center;
  
  
      &::before,
      &::after {
        content: "";
        flex: 1;
        height: 1px;
        background-color: #a8a8b3;
        margin-right: 16px;
      }
  
      &::after {
        margin-right: 0;
        margin-left: 16px;
      }
    }
  }